<template>
  <div class="d-flex justify-content-center" style="background:url('weather.png')">
    <div class="card" style="width: 18rem">
      <div class="card-body">
        <div class="input-group mb-3 warning">
          <v-text-field placeholder="Enter city name..." v-model="city"></v-text-field>
          <v-btn class="info" type="button" id="button-addon1" @click="getData">Search</v-btn>
        </div>
        <div class="card-text" v-if="!isLoading">
          <v-simple-table dark>
            <template v-slot:default>
              <tbody>
              <tr>
                <td>City Name</td>
                <td>{{ weather }}</td>
              </tr>
              <tr>
                <td>Temparature (°C)</td>
                <td>{{ temp }}</td>
              </tr>
              <tr>
                <td>Description</td>
                <td>{{ description }}</td>
              </tr>
              <tr>
                <td>Wind Speed (meter/sec)</td>
                <td>{{ wind }}</td>
              </tr>
              <tr>
                <td>Pressure(hPa)</td>
                <td>{{ pressure }}</td>
              </tr>
              <tr>
                <td>Humidity(%)</td>
                <td>{{ humidity }}</td>
              </tr>
              </tbody>
            </template>
          </v-simple-table>
        </div>
        <div class="card-text" v-if="isLoading">
          <p>retriving data from server...</p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import axios from "@/plugins/axios";

export default {
  data() {
    return {
      isLoading: false,
      city: 'Īlām',
      weather: '',
      temp: '',
    }
  },
  created() {
    this.getData();
  },
  methods: {
    getData() {
      this.isLoading = true;
      axios.get('data/2.5/weather?q=' + this.city)
          .then(({data}) => {
            this.weather = data.name;
            this.temp = data.main.temp;
            this.pressure = data.main.pressure;
            this.humidity = data.main.humidity;
            this.temp = data.main.temp;
            this.description = data.weather[0].description;
            this.wind = data.wind.speed;
            this.isLoading = false
          })
    }
  }
}
</script>